import React from 'react';
import style from './ChatAside.module.scss'

import AsideHeader from './Header/AsideHeader';
import AsideSearch from './Search/AsideSearch';
import Dialogs from './dialogsList/Dialogs';

const ChatAside = ({ currentDialog, dialogs, filter, searchVal, onSelect }) => {


    return (
        <aside className={style.aside}>
            <AsideHeader />

            <div className={style.aside__input}>
                <AsideSearch onChangeHandler={filter} searchVal={searchVal} />
            </div>

            <Dialogs currentDialog={currentDialog} onSelect={onSelect} dialogs={dialogs} />


        </aside>
    )

}

export default ChatAside;
