import React, { useState, useEffect } from 'react';

import { getDialogs, setCurrentDialogs } from '../../../../redux/reducers/dialogs';
import ChatAside from '../ChatAside';
import { connect } from 'react-redux';

const ChatAsideContainer = ({ currentDialog, dialogs, getDialogs, setCurrentDialogs }) => {



    const [searchVal, setSearchVal] = useState('')
    const [filtered, setFiltered] = useState(Array.from(dialogs))

    const onChangeHandler = e => {

        setFiltered(dialogs
            .filter(dialog => dialog.user.lastName
                .toLowerCase().indexOf(e.target.value.toLowerCase()) >= 0))
        setSearchVal(e.target.value)
    }

    useEffect(() => {
        if (!dialogs.length) {
            getDialogs()

        } else {
            setFiltered(dialogs)
        }
    }, [dialogs])



    return (
        <ChatAside
            currentDialog={currentDialog}
            onSelect={setCurrentDialogs}
            dialogs={filtered}
            searchVal={searchVal}
            filter={onChangeHandler} />
    )

}

const mapStateToProps = (state) => ({
    dialogs: state.dialogs.dialogs,
    currentDialog: state.dialogs.currentDialog,
})

export default connect(mapStateToProps, { getDialogs, setCurrentDialogs })(ChatAsideContainer);
