import React from 'react';
import style from './Chat.module.scss'


import ChatHeader from '../Header/ChatHeader';
import ChatAsideContainer from '../chatAside/chatAsideContainer/ChatAsideContainer';
import ChatContentContainer from '../chatContent/ChatContentContainer';


function Chat() {



    return (
        <div className={style.container}>
            <ChatHeader />
            <ChatAsideContainer />
            <ChatContentContainer />
        </div>
    )
}

export default Chat;
