import Axios from 'axios'



const instans = Axios.create({
    withCredentials: true,
    baseURL: 'http://localhost:3003/',

})





export const DialogsAPI = {
    getAll() {
        return instans.get(`dialogs`).then(response => response.data)
    },

}
export const MessagesAPI = {
    getDialogMessage(id) {
        return instans.get(`messages?dialog=${id}`).then(response => response.data)
    },

}

