import tinycolor from 'tinycolor2'


export default hash => {
    const [r, g, b] = hash.slice(hash.length - 4, hash.length - 1)
        .split('')
        .map(char => char.charCodeAt() > 255 ? 255 : char.charCodeAt())
    return {
        color: tinycolor({ r, g, b }).lighten(20),
        colorLight: tinycolor({ r, g, b }).lighten(0)
    }
}