
import React from 'react';
import style from './Dialogs.module.scss'
import {
    CSSTransition,
    TransitionGroup,
} from 'react-transition-group';
import orderBy from 'lodash/orderBy'

import DialogItem from '../dialog/DialogItem';
import { Empty } from 'antd';



function Dialogs({ currentDialog, userId, dialogs, onSelect }) {
    if (dialogs.length > 0) {
        return (
            <TransitionGroup component='div' className={style.dialog__list}>
                {orderBy(dialogs, 'created_at', 'desc')
                    .map(item => (
                        <CSSTransition
                            key={item._id}
                            timeout={200}
                            classNames='item'
                        >
                            <DialogItem
                                active={currentDialog === item._id}
                                message={item}
                                unReaded={0}
                                isMe={userId === item.user._id}
                                onSelect={onSelect}
                            />

                        </CSSTransition>
                    ))
                }
            </TransitionGroup>
        )
    }
    return <Empty description='No matches' />

}

export default Dialogs;
