
import React from 'react';
import { Link } from 'react-router-dom';
import { Form, Input, Button } from 'antd';
import { LockOutlined, UserOutlined } from '@ant-design/icons';

import Modal from '../modal/Modal';
import FormTitle from '../formTitle/FormTitle';
import validate from '../../../utils/validate';

function AuthForm(props) {
    const {
        values,
        touched,
        errors,
        isSubmitting,
        handleChange,
        handleBlur,
        handleSubmit,
        handleReset,
        dirty,
    } = props;


    const onFinish = values => {

        handleSubmit()
    }

    return (
        <>
            <FormTitle
                title='Войти в аккаунт'
                descr='Пожалуйста, войдите в свой аккаунт'
            />

            <Modal>

                <Form
                    initialValues={{ remember: true }}
                    onFinish={onFinish}
                >


                    <Form.Item hasFeedback
                        validateStatus={validate('name', errors, touched)}
                        help={errors.name}
                    >
                        <Input
                            id='name'
                            placeholder="Username"
                            size='large'
                            prefix={<UserOutlined />}
                            onChange={handleChange}
                            onBlur={handleBlur}
                        />
                    </Form.Item>

                    <Form.Item hasFeedback
                        validateStatus={validate('password', errors, touched)}
                        help={touched.password && errors.password}
                    >

                        <Input.Password size='large'
                            id='password'
                            placeholder="Пароль"
                            onChange={handleChange}
                            onBlur={handleBlur}
                            prefix={<LockOutlined />}
                        />

                    </Form.Item>


                    <Form.Item>
                        <Button
                            style={{ width: '100%', height: '56px', fontSize: '16px', fontWeight: 500 }} type="primary" htmlType="submit"
                        >

                            ВОЙТИ В АККАУНТ

                </Button>
                    </Form.Item>

                </Form>
                <Link to='registration'> Регистрация </Link>
            </Modal>
        </>
    )
}

export default AuthForm;
