
import React, { useRef, useEffect } from 'react';

import Messages from '../messages/Messages'
import MessagesInput from '../messagesInput/MessagesInput';
import style from './ChatContent.module.scss'
import { Empty } from 'antd';
import Preloader from '../../../assets/preloader/Preloader'


function ChatContent({ messageRef, isLoading, messages, sendMessages, currentDialog }) {




    return (
        <div className={style.content}>
            <div ref={messageRef} className={style.messages}>
                {isLoading ? <Preloader /> : messages.length > 0
                    ? messages.map(item => <Messages
                        text={item.text}
                        user={item.user}
                        date={item.date}
                        isMe={item.isMe}
                        isRead={item.isRead}
                        isTyping={item.isTyping}
                        attachments={item.attachments}
                        key={item._id}
                        audio={item.audio}
                    />
                    )
                    : <div className={style.empty__wrapper}><Empty description={!currentDialog ? 'Выберите диалог' : 'история сообщений пуста'} /></div>}

            </div>

            {currentDialog && <MessagesInput sendMessages={sendMessages} />}
        </div>
    )
}

export default ChatContent;
