
import React, { useState } from 'react';
import { Input, Button } from 'antd';
import { SmileOutlined, CameraOutlined, AudioOutlined, SendOutlined } from '@ant-design/icons'
import { UploadField } from '@navjobs/upload'
import { Picker } from 'emoji-mart'

import style from './MessagesInput.module.scss'



function MessagesInput({ sendMessages }) {

    const [value, setValue] = useState(null)
    const [emojiVisible, setEmojiVisible] = useState(false)

    const changeHandler = (e) => {
        setValue(e.target.value)
    }

    const EmojiVisibleHandler = () => {
        setEmojiVisible(!emojiVisible)
    }

    return (
        <div className={style.messages__input}>
            {emojiVisible && <div className={style.emoji__picker}>
                <Picker set='apple' />
            </div>}

            <Input prefix={
                <Button type='link'
                    onClick={EmojiVisibleHandler}
                    icon={<SmileOutlined />}
                    className={style.input__suffix}
                />}
                suffix={[
                    <UploadField
                        onFiles={files => console.log(files)}
                        containerProps={{
                            className: style.file
                        }}
                        uploadProps={{
                            accept: '.pdf,.doc,.docx,.txt,.rtf,.jpg,.jpeg,.gif,.bmp,.png,',
                            multiple: 'multiple'
                        }}
                    >
                        <Button type='link'
                            icon={<CameraOutlined />}
                            className={style.input__suffix}
                        />
                    </UploadField>,
                    !value
                        ? <Button type='link'
                            icon={<AudioOutlined />}
                            className={style.input__suffix}
                        />
                        : <Button
                            type='link'
                            icon={<SendOutlined />}
                            className={style.input__suffix + ` ${style.animate}`}
                            onClick={() => {
                                sendMessages(value)
                                setValue('')
                            }}
                        />
                ]
                }
                style={{ borderRadius: '4px' }}
                size='large'
                value={value}
                onChange={(e) => changeHandler(e)}
                placeholder='Введите текст сообщения…'
            />
        </div>
    )

}

export default MessagesInput;
