
import React from 'react';
import { Input } from 'antd';
import { SearchOutlined } from '@ant-design/icons';


function AsideSearch({ searchVal, onChangeHandler }) {



    return (
        <Input value={searchVal} onChange={onChangeHandler} placeholder={'Поиск среди контактов'} prefix={<SearchOutlined />} />
    )
}

export default AsideSearch;
