import React, { useState } from 'react';
import { Form, Input, Button } from 'antd';
import { LockOutlined, UserOutlined, MailOutlined } from '@ant-design/icons';
import { Link } from 'react-router-dom';

import Modal from '../modal/Modal';
import FormTitle from '../formTitle/FormTitle';
import ConfirmEmail from './ConfirmEmail';
import style from './RegistrationForm.module.scss'
import validate from '../../../utils/validate';

function RegistrationForm(props) {
    const {
        values,
        touched,
        errors,
        isSubmitting,
        handleChange,
        handleBlur,
        handleSubmit,
        handleReset,
        dirty,
    } = props;

    const [isSucces, setSucces] = useState(false)
    const onFinish = values => {

        handleSubmit(values)
    };

    return (
        <>
            <FormTitle
                title='Регистрация'
                descr=' Для входа в чат, вам нужно зарегистрироваться'
            />

            <Modal>
                {!isSucces
                    ? <>
                        <Form name="nest-messages" onFinish={onFinish}>

                            <Form.Item hasFeedback
                                validateStatus={validate('email', errors, touched)}
                                help={touched.email && errors.email}
                            >
                                <Input
                                    id='email'
                                    placeholder="E-Mail"
                                    size='large'
                                    prefix={<MailOutlined />}
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                />
                            </Form.Item>

                            <Form.Item hasFeedback
                                validateStatus={validate('name', errors, touched)}
                                help={touched.name && errors.name}
                            >
                                <Input
                                    id='name'
                                    placeholder="Ваше имя"
                                    size='large'
                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    prefix={<UserOutlined />}
                                />
                            </Form.Item>

                            <Form.Item hasFeedback
                                validateStatus={validate('password', errors, touched)}
                                help={touched.password && errors.password}
                            >
                                <Input.Password size='large'
                                    id='password'
                                    placeholder="Пароль"

                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    prefix={<LockOutlined />} />
                            </Form.Item>

                            <Form.Item hasFeedback
                                validateStatus={validate('confirm', errors, touched)}
                                help={touched.confirm && errors.confirm}
                            >
                                <Input.Password size='large'
                                    id='confirm'
                                    placeholder="повторите пароль"

                                    onChange={handleChange}
                                    onBlur={handleBlur}
                                    prefix={<LockOutlined />} />
                            </Form.Item>


                            <Form.Item >
                                <Button type="primary"
                                    htmlType="submit"
                                    className={style.register__btn}
                                >
                                    ЗАРЕГИСТРИРОВАТЬСЯ
                                </Button>
                            </Form.Item>

                        </Form>
                        <Link to='auth'>Войти в аккаунт</Link>

                    </>
                    : <ConfirmEmail />
                }

            </Modal>
        </>
    )

}







export default RegistrationForm;
