
import React, { useState, useRef } from 'react';


import style from './Audio.module.scss'

import audioWave from '../../../assets/img/audioWave.svg'
import pauseSvg from '../../../assets/img/pause.svg'
import playSvg from '../../../assets/img/play.svg'
import convertDurationTime from '../../../utils/convertDurationTime';





function Audio({ audio }) {

    const audioRef = useRef(null)

    const [isPlay, setIsPlay] = useState(false)

    const [duration, setDuration] = useState(null)
    const [currentTime, setCurrentTime] = useState(null)
    const [animateDuration, setAnimateDuration] = useState(null)

    const togglePlay = () => {
        setIsPlay(!isPlay)
        if (!isPlay) {
            audioRef.current.play()
        } else {
            audioRef.current.pause()

        }
    }


    const loadHendler = () => {
        setDuration(convertDurationTime(audioRef.current.duration))
        setAnimateDuration(Math.ceil(audioRef.current.duration))
    }
    const timeUpdate = () => {
        setCurrentTime(convertDurationTime(audioRef.current.currentTime))
    }

    return (<>
        <div className={style.play}
            style={{
                animationDuration: animateDuration + "s",
                animationPlayState: isPlay ? 'running' : 'paused'
            }} />


        <div className={style.audio__wrapper}>
            <audio ref={audioRef} src={audio}
                onLoadedData={loadHendler}
                onTimeUpdate={timeUpdate}
                preload='auto' onEnded={togglePlay} />

            <div className={style.audio__btn}><button onClick={togglePlay}>
                <img src={!isPlay ? playSvg : pauseSvg} /></button></div>
            <img className={style.audio__img} src={audioWave} />
            <div className={style.audio__duration}>
                {currentTime || duration}</div>
        </div></>
    )
}

export default Audio;
