import React from 'react';
import style from './Messages.module.scss'
import formatDistanceToNow from 'date-fns/formatDistanceToNow'
import ruLocale from 'date-fns/locale/ru'

import ReadCheker from '../../readCheker/ReadCheker';
import Audio from './Audio';
import Avatar from '../avatar/Avatar';



function Messages({ user, text, date, isMe, isRead, attachments, isTyping, audio }) {



    return (
        <div className={style.Messages + " " + `${isMe && style.me} ${audio && style.message_audio}`}>

            <div className={style.avatar}>
                <Avatar user={user} />
            </div>

            <div className={style.buble}>
                {(audio || text || isTyping)
                    && <div
                        className={
                            !isTyping && style.buble__content + ' ' +
                            `${isMe && !audio
                                ? style.white
                                : style.blue}`}>

                        {text && text}
                        {isTyping && <div className={style.typing__indicator}>
                            <span />
                            <span />
                            <span />
                        </div>}
                        {audio && <Audio audio={audio} />}
                    </div>}



                {attachments &&
                    <div className={style.message__attachments}>

                        {attachments.map(item => (
                            <img src={item.url}
                                className={style.message__attachment + ' '
                                    + `${(attachments.length === 1 && !text)
                                    && style.message__attachment_one}`} alt={item.filename}

                            />))}
                    </div>}




                {date && <div className={style.date}>{formatDistanceToNow(date, { addSuffix: true, locale: ruLocale })}</div>}

            </div>
            <ReadCheker isMe={isMe} isRead={isRead} />


        </div>
    )
}



export default Messages;
