

import React from 'react';

import style from './DialogItem.module.scss'
import ReadChecker from '../../../readCheker/ReadCheker'
import getMessageTime from '../../../../utils/getMessageTime';
import Avatar from '../../avatar/Avatar';


function DialogItem({ active, message, unRead, isMe, onSelect }) {

    return (


        <div onClick={() => { onSelect(message._id) }} className={style.dialog__item + ` ${active ? style.activ : ''}`}>

            <Avatar user={message.user} />
            <div className={style.dialog__item_info}>
                <div>
                    <div className={style.dialog__item_title}>
                        {message.user.lastName}
                    </div>

                    <div className={style.dialog__item_lastMessage}>
                        {message.text}
                    </div>
                </div>
                <div>
                    <div className={style.dialog__item_date}>

                        {getMessageTime(new Date(message.created_at))}
                    </div>

                    {message && unRead > 0
                        ? <div className={style.dialog__item_messageCount}>
                            {unRead > 9 ? '+9' : unRead}
                        </div>
                        : <div className={style.checked}>
                            <ReadChecker isMe={isMe} isRead={message.isRead} />
                        </div>}

                </div>
            </div>

        </div>
    )
}

export default DialogItem;
