
import React, { useEffect, useRef } from 'react';
import { connect } from 'react-redux';

import ChatContent from './ChatContent'
import { getMessages, addMessage } from '../../../redux/reducers/messages';


function ChatContentContainer({ isLoading, getMessages, addMessage, messages, currentDialog = '5eb8044f18628e8cea5095de' }) {


    useEffect(() => {
        if (currentDialog) {
            getMessages(currentDialog)
        }
    }, [currentDialog])


    let messageRef = useRef(null)

    useEffect(() => {
        messageRef.current.scrollTo(0, messageRef.current.scrollHeight)
    }, [isLoading])


    const sendMessages = (val) => {
        const message = {
            _id: messages.length++,
            user: {
                _id: "5eb8044f22c9372be5ba30fe",
                lastName: "test",
                avatar: null
            },
            created_at: new Date(),
            isMe: true,
            isRead: false,
            text: val,
            dialog: currentDialog
        }
        addMessage(message)
    }


    return (
        <ChatContent
            messageRef={messageRef}
            isLoading={isLoading}
            sendMessages={sendMessages}
            currentDialog={currentDialog}
            messages={messages} />
    )
}

const mapStateToProps = (state) => ({
    messages: state.messages.messages,
    isLoading: state.messages.isLoading,
    currentDialog: state.dialogs.currentDialog,
})

export default connect(mapStateToProps, { getMessages, addMessage })(ChatContentContainer);
