import { DialogsAPI } from "../../utils/api/api"

const initialState = {
    dialogs: [],
    currentDialog: null
}


export default (state = initialState, action) => {
    switch (action.type) {
        case 'SET_DIALOGS':
            return {
                ...state,
                dialogs: action.dialogs
            }
        case 'SET_CURRENT_DIALOGS':
            return {
                ...state,
                currentDialog: action.currentDialog
            }

        default: return state
    }
}

const DialogsActions = {
    setDialogs: (dialogs) => ({ type: 'SET_DIALOGS', dialogs }),
    setCurrentDialogs: (currentDialog) => ({ type: 'SET_CURRENT_DIALOGS', currentDialog })
}



export const getDialogs = () => async dispatch => {
    let dialogs = await DialogsAPI.getAll()
    dispatch(DialogsActions.setDialogs(dialogs))
}

export const setCurrentDialogs = (currentDialog) => async dispatch => {
    dispatch(DialogsActions.setCurrentDialogs(currentDialog))
}
