import { MessagesAPI } from "../../utils/api/api"

const initialState = {
    messages: [],
    isLoading: false

}


export default (state = initialState, action) => {
    switch (action.type) {
        case 'SET_MESSAGES':
            return {
                ...state,
                messages: action.messages
            }
        case 'CHANGE_ISLOADING':
            return {
                ...state,
                isLoading: action.isLoading

            }
        case 'ADD_MESSAGE':
            return {
                ...state,
                // messages: [action.message]
            }

        default: return state
    }
}

const MessageActions = {
    setMessages: (messages) => ({ type: 'SET_MESSAGES', messages }),
    addMessage: (message) => ({ type: 'ADD_MESSAGE', message }),
    setIsLoading: (isLoading) => ({ type: 'CHANGE_ISLOADING', isLoading }),
}



export const getMessages = (id) => async dispatch => {
    dispatch(MessageActions.setIsLoading(true))
    let messages = await MessagesAPI.getDialogMessage(id)
    dispatch(MessageActions.setMessages(messages))
    dispatch(MessageActions.setIsLoading(false))
}
export const addMessage = (message) => async dispatch => {
    // let messages = await MessagesAPI.getDialogMessage(id)

    dispatch(MessageActions.addMessage(message))
}


